import { Component  } from '@angular/core';
import {  ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import { FormControl,  FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';




@IonicPage()    
@Component({
  selector: 'page-reglamentod-create',
  templateUrl: 'reglamentod-create.html',
})
export class ReglamentodCreatePage {

 // @ViewChild('editorx') Editorx;
  public form                  : FormGroup;
  username: string = "";
  public notitulo                 : any;
  public titulo                 : any;
  public nocapitulo                 : any;
  public capitulo                 : any;
  public noarticulo                 : any;
  public articulo                 : any;


  public libro                 : any;
  public nolibro                 : any;

  public parrafo                 : any;
  public noparrafo                 : any;
  public fecha                 : any;


  private _ID                  : String;

  tiposus: any=[];
 text = 'hello world'
  item: FormControl;
  estado:any;
  public pageTitle           	: string;
  titulox:any;
  isDisablednit :any;
  constructor( public storage: Storage,  public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
     // Define form validation - VERY basic!
     this.form = this._FB.group({
      nolibro     : [''],
      libro     : [''],
        notitulo     : ['', Validators.required],
        titulo     : ['', Validators.required],
        nocapitulo     : ['', Validators.required],
        capitulo     : ['', Validators.required],
        noparrafo     : [''],
        parrafo     : [''],
        noarticulo     : ['', Validators.required],
        articulo     : ['', Validators.required],
        fecha     : ['']
      
     });



 

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }

  ionViewDidLoad() : void
  {
;
  
  this.storage.get('tiposus').then((value) => {
    this.tiposus = value.split(',');
   



});
  

if(this.navParams.get("record")==null)
{
   
 
  
  
}
else {
  
   this._ID 				=	this.navParams.data.record._id;
   this.nolibro				=	this.navParams.data.record.nolibro;
   this.libro				=	this.navParams.data.record.libro;

   this.notitulo				=	this.navParams.data.record.notitulo;
   this.titulo				=	this.navParams.data.record.titulo;
   this.nocapitulo				=	this.navParams.data.record.nocapitulo;
   this.capitulo				=	this.navParams.data.record.capitulo;
   this.noparrafo				=	this.navParams.data.record.noparrafo;
   this.parrafo				=	this.navParams.data.record.parrafo;

   this.noarticulo				=	this.navParams.data.record.noarticulo;
   this.fecha				=	this.navParams.data.record.fecha;


   this.form.controls['articulo'].setValue(this.navParams.data.record.articulo);
 //  this.texto			=	this.navParams.data.record.texto;
  

}

     
    
  }

  

  manageGallery() : void
  {
    let creat='Actualiza';
    if(this.navParams.get("record")==null)   
    {  creat='Crea';
    }

  
     let   options       : any	     = { 
    idempresa:this.navParams.data.idempresa,
    idreglamento:this.navParams.data.dataxx._id,
    libro : this.form.controls['libro'].value,
    nolibro : this.form.controls['nolibro'].value,

    notitulo : this.form.controls['notitulo'].value,
    titulo : this.form.controls['titulo'].value,
    nocapitulo : this.form.controls['nocapitulo'].value,
    capitulo : this.form.controls['capitulo'].value,
    noparrafo: this.form.controls['noparrafo'].value,
    parrafo : this.form.controls['parrafo'].value,

    noarticulo : this.form.controls['noarticulo'].value,
    articulo : this.form.controls['articulo'].value,
    fecha : this.form.controls['fecha'].value,

    nombre: this.form.controls['titulo'].value + ' ' + this.form.controls['capitulo'].value + ' ' +this.form.controls['articulo'].value,

    bitacora:{idempresa:this.navParams.data.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' reglamentod '}
    };

    
     if(this.navParams.get("record")==null)
     {  

      if(options){
        this.authService.createReg('',options,'reglamentods').then((result) => {
          if (!this.form.valid) { return; }
          this.viewCtrl.dismiss(result);
      


      }, (err) => {
        this.msj.showError3('Error',err.error);
      });
      }

      
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'reglamentods').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
   
  }



  
  cancel() {
    this.viewCtrl.dismiss();
  }
  


}



